import React from "react"
import Layout from "../../components/Layout"
import PostEntry from "../../components/PostEntry"
import Pagination from "../../components/Pagination"
import SEO from "../../components/SEO"

const Blog = ({ pageContext }) => {
  const { nodes, category, pageNumber, hasNextPage, itemsPerPage, allPosts } = pageContext

  return (
    <Layout>
      <SEO
        title="Blog"
        description="Blog posts"
        keywords={[`blog`]}
        seoImage={'https://www.sharkhung.vn/ogimage-default.jpg'}
      />
      <div className="category">
        <div className="container">
          <div className="list-post w-100">
            <div className="row">
            {(() => {
              if (category) {
                return (
                  <>{category && category.map(post => <PostEntry key={post.postId} post={post} />)}</>
                )
              } else {
                return (
                  <>{nodes && nodes.map(post => <PostEntry key={post.postId} post={post} />)}</>

                )
              }
            })()}
              

              <Pagination
                pageNumber={pageNumber}
                hasNextPage={hasNextPage}
                allPosts={allPosts}
                itemsPerPage={itemsPerPage}
              />
            </div>
          </div>
        </div>
      </div>
    </Layout>

  )
}

export default Blog
